@import 'mixins';
@import 'vars';

@import 'components/page-wrapper/page-wrapper';
@import 'components/primary-nav/primary-nav';
@import 'components/panel/panel';
@import 'components/panel/inner';
@import 'components/page-header/page-header';
@import 'components/bio/bio';
@import 'components/gallery/gallery';
@import 'components/shows/shows';

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    height: 100%;
    color: #333;
    font-family: Open Sans;
    font-size: 18px;
    position: relative;
}

ul {
    padding: 0;
    margin: 0;
}

h1 {
    margin: 0;
    font-size: 36px;
}

.beginning {
    font-size: 65px;
    color: $orange;
    text-shadow: 2px 5px 5px rgba(0, 0, 0, 0.2);

    @media screen and (min-width: 768px) {
        font-size: 80px;
    }
}

.middle {
    font-size: 30px;
    color: $orange;
    text-shadow: 2px 5px 5px rgba(0, 0, 0, 0.2);
}

.end {
    margin-top: 60px;
    font-size: 65px;
    color: white;
    text-shadow: 2px 5px 5px rgba(0, 0, 0, 0.2);

    @media screen and (min-width: 768px) {
        font-size: 95px;
    }
}